import Ppt from '@/components/ppt/ppt.vue'
import star from '@/components/star.vue'
import Comment from '@/components/comment.vue'
import {
	getDetail,
	getComment
} from '@/api/detail.js'
import {
	getImgSrc
} from '@/utils/comUtils.js'
export default {
	components: {
		Ppt,
		star,
		Comment
	},
	data() {
		return {
			id: '', //当前页id
			bannerList: [],
			commentList:[],
		}
	},

	onLoad(params) {
		uni.setNavigationBarTitle({
			title: "美食"
		})
		// this.id = params.id
		this.id = 279
		this.getDetailData();
	},
	methods: {
		getDetailData() {
			this.getDetail();
			this.getComment();
		},
		// 获取详情页数据
		getDetail(){
			getDetail(this.id).then(res => {
				if (res.data.success) {
					res = res.data.obj;
					// 图片处理
					res.atts = getImgSrc(res.atts)
			
					// 生成ppt数据格式
					this.bannerList = res.atts.map(item => {
						return {
							atts: item
						}
					})
			
			
				}
			}).catch(err => console.log(err))
		},
		// 获取评论数据
		getComment(){
			getComment(this.id).then(res=>{
				if(res.data.success){
					res=res.data.obj;
					this.commentList=res;
				}
			}).catch(err => console.log(err))
		}
	}
}
